import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-activity',
  templateUrl: './activity.page.html',
  styleUrls: ['./activity.page.scss'],
})
export class ActivityPage implements OnInit {
response:any;
currentColor:any;
theme:any;
  constructor(public router: Router) {
    const data = this.router.getCurrentNavigation().extras.state.activity;
    this.theme = this.router.getCurrentNavigation().extras.state.theme;
    this.response=data;
    this.currentColor='light'
   }

  ngOnInit() {

  }


  NextPageActivity(data){
    this.router.navigate(['/menu/first/tabs/tab1/activity/activity-next'], { state: { data: data } });
  }



}
